.sidebar {
    display: none;
    width: 25%;
    height: 100vh;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 10px 40px 0 rgba(29, 33, 38, 0.13);
    background: #fff;
    position: fixed;
    left: 0;
    z-index: 999;
    padding: 15px 25px 16px 70px;
}

.sidebar.open {
    display: flex;
    flex-direction: column;
    gap: 85px;
}

.background {
    background: #000;
    opacity: 0.3;
    position: fixed;
    z-index: 1;
    width: 100vw;
    height: 100vh;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
    height: 100%;
}

.nav a {
    text-decoration: none;
    font-weight: 600;
    font-size: 22px;
    color: #000;
}

.nav a:hover {
    font-weight: 600;
    font-size: 22px;
    color: #58acff;
}


.nav a:active {
    color: #3d6b99;
}

.nav a.active {
    font-weight: 600;
    font-size: 22px;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #0059b2;

}

.close {
    border: none;
    align-self: end;
    background: unset;
}

.close img {
    width: 24px;
    height: 24px;
}
