.friendMessage {
    display: flex;
    flex-direction: column;
}

.friendImageAndText {
    display: flex;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
}

.friendText {
    position: relative;
    max-width: 527px;
    border-radius: 10px 10px 0 10px;
    display: flex;
    flex-direction: column;
    padding: 4px 24px 8px 12px;
    gap: 4px;
    background-image: url("../../../../public/my_back_mess.svg");
    background-repeat: no-repeat;
    background-position: center;
}

.friendText:after {

}

.friendName {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 16px;
    color: #000;
}

.friendMessageText {
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: #000;
    white-space: pre-line;
    word-break: break-word;
}

.friendTime {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 10px;
    color: #000;
}
