.button {
    padding: 5px 24px;
}

.disabled {
    background: #004d99;
    opacity: 0.5;
    border-radius: 3px;
}

.secondary {
    border: 1px solid #06c;
    border-radius: 3px;
    padding: 5px 24px;
}

.default {
    border-radius: 3px;
    padding: 5px 24px;
    background: #06c;
}

.red {
    border-radius: 3px;
    padding: 5px 24px;
    background: #cc1439;
}

.default:hover {
    background: #0080ff;
    cursor: pointer;
}

.red:hover {
    background: #ff1a47;
    cursor: pointer;
}

.secondary:hover {
    background: #e5f0fa;
    cursor: pointer;
}

.default:active {
    background: #0059b2;
}

.secondary:active {
    background: #c9e5ff;
}

.red:active {
    background: #b21232;
}

.button:disabled {
}
