.message {
    display: flex;
    flex-direction: column;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
    position: relative;
    max-width: 527px;
    border-radius: 10px 10px 0 10px;
    display: flex;
    flex-direction: column;
    padding: 4px 24px 8px 12px;
    gap: 4px;
    background-image: url("../../../../public/friend_back_mess.svg");
    background-repeat: no-repeat;
    background-position: center;

}

.text:after {
}

.name {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 18px;
    color: #fff;
    align-self: end;
}

.messageText {
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: #fff;
    white-space: pre-line;
    word-break: break-word;
}

.time {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 10px;
    color: #000;
    text-align: end;
}